import Vue           from 'vue'
import Router        from 'vue-router'
import Weclome       from '@/common/weclome' //
import LogoVideo     from '@/common/index/logoVideo' //
import SwitchRoles   from '@/common/index/switchRoles' //
import From          from '@/common/index/from' // 
import SearchIndex   from '@/common/index/search' // 
import Homepage      from '@/common/homepage' //
import Search        from '@/common/search' //
import Profile       from '@/common/profile' //
import ProfileEditor from '@/common/profileEditor' //
import Video         from '@/common/video' //
import Lesson        from '@/common/lesson' //
import LessonItem    from '@/common/lessonItem' //
import Team          from '@/common/team' //
import Group         from '@/common/group' //
import Navbar        from '@/components/navbar'
import Popout        from '@/components/popout' //
import Calendar      from '@/common/calendar' //
import Activity      from '@/common/activity' //
import Course        from '@/common/course' //play/ 

Vue.use(Router)
Vue.component('v-popout', Popout)
Vue.component('v-nav', Navbar)


const routers = [
	{
		path      : '/',
		component : Weclome,
		children  : [{
			hidden    : true,
			path      : '/cutvideo',
			component : LogoVideo
		},{
			hidden    : true,
			path      : '/switchrole',
			component : SwitchRoles
		},{
			hidden    : true,
			path      : '/from',
			component : From
		},{
			hidden    : true,
			path      : '/searchindex',
			component : SearchIndex
		}]

	},
	{
		path      : '/home',
		component : Homepage
	},
	{
		path      : '/search',
		component : Search
	},
	{
		path      : '/profile',
		component : Profile
	},
	{
		path      : '/editor',
		component : ProfileEditor
	},
	{
		path      : '/video',
		component : Video
	},
	{
		path      : '/lesson',
		component : Lesson
	},
	{
		path      : '/lessonItem',
		component : LessonItem
	},
	{
		path      : '/team',
		component : Team
	},
	{
		path      : '/group',
		component : Group
	},
	{
		path      : '/activity',
		component : Activity
	},
	{
		path      : '/course',
		component : Course
	},
	{
		path      : '/calendar',
		component : Calendar
	}
]


const router = new Router({
	// mode: 'history',
	routes : routers,
})

router.beforeEach((to, from, next) => {
	document.body.scrollTop = 0;
	next();
})

export default router;


