<!--    -->
<template>
	<div class="switchrole">
		<p class="title"></p>
		<div v-for="(item, index) in roles" :key="index" class="item">
			<div class="btn" v-text="item.title" @click="showModal(item)"></div>
		</div>
		<Modal
			width="80"
	        v-model="modal"
	        :closable="false">
	        <p v-text="selectRole.text"></p>
	        <div slot="footer" class="roles_footer">
	            <Button type="default" size="large" class="button" @click="onCanel"></Button>
	            <Button type="default" size="large" class="button" :loading="loading" @click="onSwitch"></Button>
	        </div>
	    </Modal>
	</div>
</template>

<script>
	import Vue                    from 'vue';
	import axios                  from 'axios';
	import {mapState, mapActions} from 'vuex';
	export default{
		name : 'switchroles',
		data() {
			return {
				modal      : false,
				show       : false,
				loading : false,
				selectRole : {},
				roles      : [
					{
						type : 0,
						title : '',
						text : ''
					},{
						type : 1,
						title : '',
						text : ''
					},{
						type : 2,
						title : '',
						text : ''
					}
				]
			}
		},
		computed : mapState({
			user     : state => state.User.user,
			token    : state => state.User.token,
			is_login : state => state.User.isLogin,
        }),
		methods: {
			...mapActions([
	            'onShowNav',
	            'onSetIdentity'
	        ]),
			toPage(path) {
				this.$router.push({path});
			},
			showModal(item) {
				this.selectRole = item;
				this.modal      = true;
			},
			onSwitch() {
				this.loading = true;
				this.getIdentity();

			},
			onCanel() {
				this.modal   = false;
				this.loading = false;
			},
			getIdentity() {
				console.log(Vue.setting.api + '/user')
				axios.put(Vue.setting.api + '/user',{
					user : this.user._id,
					type : this.selectRole.type
				})
				.then(result => result.data)
				.then(result => {
					console.log('', result)
					this.onCanel();
					this.onSetIdentity(result.identity);
					this.$Message.success({
						duration : 1,
						content : `${this.selectRole.title}`,
						onClose : () => {
							// this.$router.push({path : '/from', query : {type : this.selectRole.type}});
							if(result.identity) {
								this.$router.push({path : 'home'});
							} else this.$router.push({path : '/from', query : {type : this.selectRole.type}});
							
						}
					});

				})
				.catch(err => {
					this.onCanel();
					this.$Message.warning('');
				})
			}
		},
		mounted() {
		}
	}
</script>

<style scoped>
.switchrole {
	width      : 50vw;
}
.switchrole .title {
	font-size   : 10pt;
	font-weight : 900;
	color       : #313131;
}
.switchrole .item {
	margin : 1em 0;
}
.switchrole .item .btn {
	margin           : 0 auto;
	width            : 40vw;
	padding          : 1.5vh 0;
	font-size        : 12pt;
	color            : #fff;
	background-color : #313131;
	border-radius    : 5px;
}
.roles_footer {
	display         : flex;
	justify-content : center;
	align-items     : center;
	flex-direction  : row;
}
</style>


